/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
*, *:before, *:after {
  box-sizing: border-box; }

:root {
  --dark-gray: #333;
  --medium-gray: #383838;
  --gray: #5F6A72;
  --light-gray: #707679;
  --dark-green: #18597D;
  --yellow: #e3b54d;
  --purple: #4A4857;
  --navy: #505C70;
  --blue: #26A0CD;
  --light-blue: #47abd0;
  --orange: #e2725d;
  --light-orange: #e87d68; }

html,
body,
.app {
  height: 100%; }

.app-wrap {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(15deg, #13547a 0%, #80d0c7 100%); }

.calculator {
  width: 320px;
  height: 445px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1fr 30px repeat(5, 1fr);
  grid-gap: 2px;
  background-color: #333;
  background-color: var(--dark-gray);
  border: 1px solid #333;
  border: 1px solid var(--dark-gray);
  border-top-left-radius: 15px;
  overflow: hidden;
  box-shadow: 6px 6px 15px 1px rgba(24, 89, 125, 0.5); }
  .calculator__result {
    grid-column: 1 / span 4;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    overflow: hidden;
    background-color: #333;
    background-color: var(--dark-gray);
    text-overflow: ellipsis;
    white-space: nowrap;
    font-family: 'Roboto', sans-serif;
    font-size: 32px;
    font-weight: 500;
    color: white;
    padding-right: 30px; }
  .calculator__history {
    grid-column: 1 / span 4;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: relative;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    color: white;
    font-weight: 700;
    background-color: #383838;
    background-color: var(--medium-gray);
    padding: 0 15px 0 60px; }
    .calculator__history li {
      padding-left: 5px; }
    .calculator__history::before {
      content: 'HISTORY';
      position: absolute;
      left: 10px;
      bottom: 9px;
      font-size: 10px;
      font-weight: 500;
      color: #e3b54d;
      color: var(--yellow); }
    .calculator__history.is-hide::before {
      display: none; }
  .calculator__btn {
    font-family: 'Roboto', sans-serif;
    font-size: 32px;
    font-weight: 700;
    text-shadow: 1px 1px 0px #333333;
    border: none;
    background-color: #4A4857;
    background-color: var(--purple);
    color: white;
    cursor: pointer;
    transition: .1s all ease;
    outline: none;
    position: relative;
    padding: 0; }
    .calculator__btn:hover {
      background-color: #505C70;
      background-color: var(--navy); }
    .calculator__btn:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      border: 1px solid white;
      opacity: 0; }
    .calculator__btn:focus:before {
      -webkit-animation: border-animation 0.2s ease-out;
              animation: border-animation 0.2s ease-out; }
    .calculator__btn--operator {
      background: #5F6A72;
      background: var(--gray); }
      .calculator__btn--operator:hover {
        background-color: #707679;
        background-color: var(--light-gray); }
    .calculator__btn--cero {
      grid-column: 1 / span 2; }
    .calculator__btn--empty {
      pointer-events: none; }
    .calculator__btn--reset {
      background: #26A0CD;
      background: var(--blue);
      grid-column: 1 / span 2; }
      .calculator__btn--reset:hover {
        background-color: #47abd0;
        background-color: var(--light-blue); }
    .calculator__btn--equal {
      background: #e2725d;
      background: var(--orange);
      grid-column: 3 / span 2; }
      .calculator__btn--equal:hover {
        background-color: #e87d68;
        background-color: var(--light-orange); }

@-webkit-keyframes border-animation {
  0% {
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes border-animation {
  0% {
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0; } }

