*, *:before, *:after {
	box-sizing: border-box;
}

:root {
--dark-gray: #333;
--medium-gray: #383838;
--gray: #5F6A72;
--light-gray: #707679;
--dark-green: #18597D;
--yellow: #e3b54d;
--purple: #4A4857;
--navy: #505C70;
--blue: #26A0CD;
--light-blue: #47abd0;
--orange: #e2725d;
--light-orange: #e87d68;
}

html, 
body,
.app {
	height: 100%;
}

.app-wrap {
height: 100%;
width: 100%;
display: flex;
justify-content: center;
align-items: center;
background-image: linear-gradient(15deg, #13547a 0%, #80d0c7 100%);
}